import { Component, OnInit } from '@angular/core';
import { WeatherService } from '../weather.service';
import { IpService } from '../ip.service';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent implements OnInit {
  ip = '';
  txtCity = '';
  city = 'SaiGon';
  nhietdo = 0;
  isLoading = false;
  constructor(private weatherService: WeatherService, private ipService: IpService) { }

  ngOnInit() {
    this.ipService.getIp()
      .then(ip => this.ip = ip)
      .catch(err => console.log(err));

    this.weatherService.getWeather(this.city)
      .then(weather => { this.nhietdo = weather.main.temp; console.log(weather); })
      .catch(err => console.log(err));
  }

  getWeather(city) {
    this.isLoading = true;
    this.city = city;
    this.weatherService.getWeather(city)
      .then(weather => {
        this.nhietdo = weather.main.temp;
        this.isLoading = false;
        console.log(weather);
      })
      .catch(err => {
        alert('Can get temp from your city you input');
        this.isLoading = false;
        this.city = '';
        this.txtCity = '';
      }
      );
  }

  loadMessage() {
    if (this.isLoading) {
      return 'Loading...';
    }
    return this.city === '' ? '' : this.city + ' is now ' + this.nhietdo + 'oC';
  }

  loading() {
    if (this.isLoading) {
      return `Loading...`;
    }
    return '';
  }

}



