import { Component, OnInit, keyframes } from '@angular/core';

@Component({
  selector: 'app-tu-vung',
  templateUrl: './tu-vung.component.html',
  styleUrls: ['./tu-vung.component.css']
})
export class TuVungComponent implements OnInit {

  arrWords = [
    { id: 1, en: 'action', vn: 'hnh ng', memorized: true },
    { id: 2, en: 'actor', vn: 'din vin', memorized: false },
    { id: 3, en: 'activity', vn: 'hot ng', memorized: true },
    { id: 4, en: 'active', vn: 'ch ng', memorized: true },
    { id: 5, en: 'bath', vn: 'tm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phng ng', memorized: true }
  ];

  newEn = '';
  newVi = '';
  addFirst = true;
  hideForm = true;
  filterStatus = 'ALL';

  toggeMemorized(id) {
    const index = this.findIndex(id, this.arrWords);
    this.arrWords[index].memorized = !this.arrWords[index].memorized;
  }

  findIndex(id, arr) {
    return arr.findIndex(item => item.id === id);
  }

  getShowStatus(memorized: boolean) {
    const ifALL = this.filterStatus === 'ALL';
    const ifMEMORIZED = this.filterStatus === 'MEMORIZED' && memorized;
    const ifUNMEMORIZED = this.filterStatus === 'UNMEMORIZED' && !memorized;
    return ifALL || ifMEMORIZED || ifUNMEMORIZED;
  }

  remove(id: number) {
    const index = this.findIndex(id, this.arrWords);
    this.arrWords.splice(index, 1);
  }

  toggleForm() {
    this.hideForm = !this.hideForm;
  }

  handleChangeCheckbox(event) {
    this.addFirst = event.target.checked;
  }

  add() {
    if (!this.hideForm) {
      const en = this.newEn;
      const vn = this.newVi;
      const arr = this.arrWords;

      if (en !== '' && vn !== '') {
        const newWord = { id: arr.length + 1, en: en, vn: vn, memorized: false };
        const kqkt = this.checkExists(newWord, arr);

        if (kqkt) {
          alert(' c t ' + newWord.en);
        } else {
          this.addFirst ? arr.unshift(newWord) : arr.push(newWord);
          this.newEn = '';
          this.newVi = '';
          this.toggleForm();
        }

      } else {
        alert('Vui lng in  thng tin.');
      }
    } else {
      this.toggleForm();
    }
  }

  checkExists(newWord, listWords) {
    for (let i = 0; i < listWords.length; i++) {
      if (listWords[i].en === newWord.en) {
        return true;
      }
    }
    return false;
  }


  constructor() { }

  ngOnInit() {
  }

}



