import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-word',
  templateUrl: './word.component.html',
  styleUrls: ['./word.component.css']
})

export class WordComponent {
  name = 'Tran van tot';
  age = 30;
  imgUrl = 'https://scontent.fsgn5-4.fna.fbcdn.net/v/t1.0-9/25659969_563066234025628_8342423636721582839_n.jpg?oh=a5a0a145e0f787344df27ad9c9020d57&oe=5AF912D3';
  showTitle = true;

  toggleTitle() {
    this.showTitle = !this.showTitle;
  }
}



