import { Component } from '@angular/core';
import { SignInService } from './sign-in.service';

// <input placeholder="email@gmail.com" ngModel name="email" required/>
// <p *ngIf="formSignIn.controls.email?.errors?.required">Email is required</p>
@Component({
  selector: 'app-sign-in',
  template: `
    <form (ngSubmit)="onSubmit(formSignIn)" #formSignIn="ngForm">
      <input
        placeholder="email@gmail.com"
        ngModel
        #txtEmail="ngModel"
        name="email"
        required
        email
      />
      <p
        *ngIf="txtEmail.errors?.required"
        >Email is required
      </p>
      <input
        type="password"
        placeholder="Password"
        ngModel
        #txtPassword="ngModel"
        name="password"
        minlength="6"
        pattern="[a-z]*"
      />
      <div ngModelGroup="subjects">
        <label><input type="checkbox" [ngModel]="false" name="nodejs" /> nodejs</label>
        <label><input type="checkbox" [ngModel]="false" name="angularjs" /> angularjs</label>
        <label><input type="checkbox" [ngModel]="false" name="reactjs" /> reactjs</label>
      </div>
      <button>Submit</button>
    </form>
    <button (click)="postToServer();">Post</button>
    <p>{{ txtEmail.errors | json }}</p>
    <p>{{ txtPassword.errors | json }}</p>
    <p [style.color]="txtEmail.errors?.required ? 'red' : 'green' " *ngIf="txtEmail.touched">{{ txtEmail.errors?.required ? 'Email khong de trong' : 'Ok' }}</p>
    <p [style.color]="txtEmail.errors?.email ? 'red' : 'green' " *ngIf="txtEmail.touched">{{ txtEmail.errors?.email ? 'Email chua dung' : 'Ok' }}</p>
    <p>{{ formSignIn.value | json }}</p>
  `,
  providers: [SignInService]
})

export class SignInComponent {
  email = '';
  password = '';

  constructor(private signInService: SignInService) { }

  onSubmit(formSignIn) {
    // console.log(formSignIn);
    // throw new Error('Form is valid');
    this.signInService.getService(formSignIn.value)
      .then(result => console.log(result))
      .catch(err => console.log(err));
  }

  postToServer() {
    // const url = 'http://localhost:3000/signin/';
    // const headers = new Headers({ 'Content-type': 'application/json' });
    // const body = JSON.stringify({ name: 'Khoa pham' });
    // this.http.post(url, body, { headers })
    //   .toPromise()
    //   .then(res => res.text())
    //   .then(resText => console.log(resText));
  }
}



